    font-weight: 700;
    color: white;
    box-shadow: 0 4px 20px rgba(0, 102, 255, 0.3);
    background-size: cover;
    background-position: center;
}

.avatar-upload-controls {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.avatar-hint {
    font-size: 12px;
    color: var(--gray);
    text-align: center;
    margin: 0;
}

/* ===== Profile Page Additions ===== */
.profile-divider {
    height: 1px;
    background: rgba(255, 255, 255, 0.08);
    margin: 30px 0;
    width: 100%;
}

.achievements-section {
    text-align: left;
    width: 100%;
    padding-bottom: 20px;
}

.achievements-title {
    font-size: 15px;
    color: white;
    margin-bottom: 12px;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-family: 'Orbitron', monospace;
}

.achievement-category-section {
    margin-bottom: 30px;
}

.achievement-category-section:last-child {
    margin-bottom: 0;
}

.achievement-category-title {
    font-size: 14px;
    color: var(--gray);
    margin-bottom: 16px;
    font-weight: 700;
    letter-spacing: 0.5px;
    font-family: 'Orbitron', monospace;
}


.achievements-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 16px;
    width: 100%;
}

.achievement-card {
    background: rgba(255, 255, 255, 0.03);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    padding: 16px;
    transition: all 0.3s ease;
    aspect-ratio: 1 / 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    overflow: hidden;
}

.achievement-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.05) 0%, rgba(255, 255, 255, 0) 100%);
    opacity: 0;
    transition: opacity 0.3s ease;
}

.achievement-card:hover::before {
    opacity: 1;
}

.achievement-card.unlocked {
    background: linear-gradient(135deg, rgba(0, 102, 255, 0.15) 0%, rgba(0, 102, 255, 0.05) 100%);
    border-color: rgba(0, 102, 255, 0.4);
    box-shadow: 0 4px 15px rgba(0, 102, 255, 0.2);
}

.achievement-card.unlocked:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(0, 102, 255, 0.3);
}

.achievement-card.locked {
    opacity: 0.4;
}

.achievement-card.locked:hover {
    opacity: 0.5;
}

.achievement-card.secret.locked .achievement-name,
.achievement-card.secret.locked .achievement-description {
    color: var(--gray);
    font-style: italic;
}

.achievement-card-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 8px;
}

.achievement-name {
    font-size: 14px;
    font-weight: 700;
    color: white;
    line-height: 1.3;
    letter-spacing: 1px;
    font-family: 'Orbitron', monospace;
}

.achievement-icon {
    position: absolute;
    top: 12px;
    right: 12px;
    width: 18px;
    height: 18px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-radius: 3px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0;
    transition: all 0.3s ease;
}

.achievement-card.unlocked .achievement-icon {
    background: white;
    border-color: rgba(255, 255, 255, 0.3);
}

.achievement-description {
    font-size: 12px;
    color: var(--light-gray);
    line-height: 1.5;
    letter-spacing: 1px;
    font-family: 'Orbitron', monospace;
}

@media (max-width: 768px) {
    .achievements-grid {
        grid-template-columns: 1fr;
    }
}

