B,SAAS;IACT,WAAW;AACf;;AAEA;IACI,qCAAqC;IACrC,0CAA0C;IAC1C,mBAAmB;IACnB,aAAa;IACb,yBAAyB;IACzB,mBAAmB;IACnB,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,WAAW;IACX,kBAAkB;IAClB,MAAM;IACN,OAAO;IACP,QAAQ;IACR,SAAS;IACT,8FAA8F;IAC9F,UAAU;IACV,6BAA6B;AACjC;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,6FAA6F;IAC7F,oCAAoC;IACpC,6CAA6C;AACjD;;AAEA;IACI,2BAA2B;IAC3B,6CAA6C;AACjD;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,YAAY;AAChB;;AAEA;;IAEI,kBAAkB;IAClB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,uBAAuB;IACvB,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,YAAY;IACZ,gBAAgB;IAChB,mBAAmB;IACnB,kCAAkC;AACtC;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,WAAW;IACX,WAAW;IACX,YAAY;IACZ,0CAA0C;IAC1C,kBAAkB;IAClB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,YAAY;IACZ,yBAAyB;AAC7B;;AAEA;IACI,iBAAiB;IACjB,sCAAsC;AAC1C;;AAEA;IACI,eAAe;IACf,wBAAwB;IACxB,gBAAgB;IAChB,mBAAmB;IACnB,kCAAkC;AACtC;;AAEA;IACI;QACI,0BAA0B;IAC9B;AACJ","sourcesContent":["/* ===== Reset ===== */\r\n* {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n}\r\n\r\n/* ===== Variables ===== */\r\n:root {\r\n    --blue: #0066FF;\r\n    --black: #141417;\r\n    --gray: #666666;\r\n    --light-gray: #999999;\r\n    --border: #21262d;\r\n    --green: #00C853;\r\n    --orange: #FF9800;\r\n    --red: #FF5252;\r\n}\r\n\r\n/* ===== Base ===== */\r\nbody {\r\n    font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;\r\n    background: var(--black);\r\n    color: white;\r\n    -webkit-font-smoothing: antialiased;\r\n}\r\n\r\n/* ===== Header ===== */\r\n.header {\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    right: 0;\r\n    padding: 25px 30px;\r\n    background: var(--black);\r\n    border-bottom: 1px solid var(--border);\r\n    z-index: 100;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n}\r\n\r\n.logo {\r\n    font-size: 14px;\r\n    font-weight: 400;\r\n    color: var(--light-gray);\r\n    letter-spacing: 2px;\r\n    text-transform: uppercase;\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 16px;\r\n}\r\n\r\n.logo-image {\r\n    height: 28px;\r\n    width: auto;\r\n}\r\n\r\n.header-right {\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 20px;\r\n}\r\n\r\n.icon-link, .icon-button {\r\n    color: white;\r\n    background: none;\r\n    border: none;\r\n    cursor: pointer;\r\n    padding: 8px;\r\n    border-radius: 50%;\r\n    transition: background 0.2s;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n}\r\n\r\n.icon-link:hover, .icon-button:hover {\r\n    background: rgba(255, 255, 255, 0.1);\r\n}\r\n\r\n/* Sidebar Menu */\r\n.sidebar-overlay {\r\n    display: none;\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    background: rgba(0, 0, 0, 0.5);\r\n    z-index: 1000;\r\n    opacity: 0;\r\n    transition: opacity 0.3s ease;\r\n}\r\n\r\n.sidebar-overlay.show {\r\n    display: block;\r\n    opacity: 1;\r\n}\r\n\r\n.sidebar-content {\r\n    position: fixed;\r\n    top: 0;\r\n    right: 0;\r\n    width: 300px;\r\n    height: 100%;\r\n    background: #1c1c21;\r\n    border-left: 1px solid #30363d;\r\n    transform: translateX(100%);\r\n    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n\r\n.sidebar-overlay.show .sidebar-content {\r\n    transform: translateX(0);\r\n}\r\n\r\n/* Modal (Center) */\r\n.modal {\r\n    display: none;\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    background: rgba(0, 0, 0, 0.8);\r\n    z-index: 1000;\r\n    justify-content: center;\r\n    align-items: center;\r\n    opacity: 0;\r\n    transition: opacity 0.3s ease;\r\n}\r\n\r\n.modal.show {\r\n    display: flex;\r\n    opacity: 1;\r\n}\r\n\r\n.modal-content {\r\n    background: #1c1c21;\r\n    border: 1px solid #30363d;\r\n    border-radius: 20px; /* More rounded */\r\n    width: 90%;\r\n    max-width: 1000px; /* Wider for 2-column layout */\r\n    margin: auto;\r\n    position: relative;\r\n    transform: translateY(20px);\r\n    transition: transform 0.3s ease;\r\n}\r\n\r\n.modal.show .modal-content {\r\n    transform: translateY(0);\r\n}\r\n\r\n.modal-header {\r\n    padding: 30px 50px; /* More padding */\r\n    border-bottom: 1px solid #30363d;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n}\r\n\r\n.modal-header h2 {\r\n    margin: 0;\r\n    font-size: 28px; /* Larger title */\r\n}\r\n\r\n.sidebar-header {\r\n    padding: 30px 30px 25px; /* Increased top padding */\r\n    border-bottom: 1px solid #30363d;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n}\r\n\r\n.sidebar-header h2 {\r\n    margin: 0;\r\n    font-size: 24px;\r\n}\r\n\r\n.close-button {\r\n    background: none;\r\n    border: none;\r\n    color: var(--gray);\r\n    font-size: 28px; /* Larger close button */\r\n    cursor: pointer;\r\n    padding: 0;\r\n    line-height: 1;\r\n}\r\n\r\n.close-button:hover {\r\n    color: white;\r\n}\r\n\r\n.modal-body {\r\n    padding: 50px; /* More padding */\r\n}\r\n\r\n.sidebar-body {\r\n    padding: 30px;\r\n    overflow-y: auto;\r\n}\r\n\r\n.sidebar-link {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    color: white;\r\n    text-decoration: none;\r\n    padding: 12px 16px;\r\n    background: rgba(255, 255, 255, 0.05);\r\n    border: 1px solid rgba(255, 255, 255, 0.2);\r\n    border-radius: 8px;\r\n    transition: background 0.2s;\r\n    font-size: 14px;\r\n    text-transform: none;\r\n    letter-spacing: 0.5px;\r\n}\r\n\r\n.sidebar-link:hover {\r\n    background: rgba(255, 255, 255, 0.1);\r\n}\r\n\r\n#resetHistoryButton {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    width: 100%;\r\n    padding: 12px 16px;\r\n    background: rgba(248, 81, 73, 0.1);\r\n    color: #f85149;\r\n    bo