ng: 1px;\r\n}\r\n\r\n.step h3 {\r\n    font-size: 22px;\r\n    font-weight: 700;\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.step p {\r\n    font-size: 15px;\r\n    color: var(--gray);\r\n    line-height: 1.6;\r\n}\r\n\r\n/* ===== Challenge Section ===== */\r\n.challenge-section {\r\n    padding: 80px 40px;\r\n    text-align: center;\r\n}\r\n\r\n.challenge-text {\r\n    font-size: 22px;\r\n    color: var(--gray);\r\n    line-height: 1.6;\r\n    max-width: 600px;\r\n    margin: 0 auto;\r\n}\r\n\r\n/* ===== Footer ===== */\r\n.footer {\r\n    padding: 50px 40px;\r\n    text-align: center;\r\n    color: var(--gray);\r\n    font-size: 14px;\r\n    border-top: 1px solid var(--border);\r\n}\r\n\r\n/* ===== Result Overlay ===== */\r\n.result-overlay {\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    background: rgba(0, 0, 0, 0.9);\r\n    display: none;\r\n    align-items: center;\r\n    justify-content: center;\r\n    z-index: 1000;\r\n    animation: fadeIn 0.3s ease;\r\n}\r\n\r\n@keyframes fadeIn {\r\n    from { opacity: 0; }\r\n    to { opacity: 1; }\r\n}\r\n\r\n.result-content {\r\n    text-align: center;\r\n    padding: 60px 40px;\r\n    border-radius: 16px;\r\n    background: rgba(255, 255, 255, 0.05);\r\n    backdrop-filter: blur(10px);\r\n    border: 1px solid var(--border);\r\n    max-width: 500px;\r\n    animation: slideUp 0.4s ease;\r\n}\r\n\r\n@keyframes slideUp {\r\n    from {\r\n        opacity: 0;\r\n        transform: translateY(30px);\r\n    }\r\n    to {\r\n        opacity: 1;\r\n        transform: translateY(0);\r\n    }\r\n}\r\n\r\n.result-message {\r\n    font-size: 28px;\r\n    font-weight: 700;\r\n    margin-bottom: 30px;\r\n    line-height: 1.4;\r\n}\r\n\r\n.result-content.perfect .result-message {\r\n    color: var(--green);\r\n}\r\n\r\n.result-content.excellent .result-message {\r\n    color: var(--blue);\r\n}\r\n\r\n.result-content.good .result-message {\r\n    color: var(--orange);\r\n}\r\n\r\n.r